<!-- eslint-disable vue/valid-v-slot -->
<template>
    <div class="conteiner-view">
      <modal-create-sell
      @sellerModalMethod="sellerModalMethod"
      :showModalSeller="showModalSeller"></modal-create-sell>
      <div class="header-container">
        <header-views title="Vendedores"></header-views>
        <v-btn class="create-button" elevation="0" @click="newSellerModal = true">
          CREAR VENDEDOR
        </v-btn>
      </div>
      <v-card class="table-conteiner" elevation="0">
        <v-card-title class="title-table">
          <v-text-field outlined v-model="search" prepend-inner-icon="mdi-magnify" label="Buscar" hide-details dense
            placeholder="Buscar"></v-text-field>
          <v-spacer></v-spacer>
        </v-card-title>
        <div class="data-table-permissions">
          <v-data-table
          class="table-permissions"
          dense
          :items="items"
          :headers="headers"
          :search="search"
          :items-per-page="5">
          </v-data-table>
        </div>
      </v-card>
    </div>
  </template>

<script lang="ts">
import { Component, Vue } from 'vue-property-decorator'
import { useSellerStore } from '@/store/sellers'
import { ISeller } from '@/domain/models/sellerModel'
import ModalCreateSell from '@/components/ModalCreateSell.vue'

  @Component({
    components: {
      ModalCreateSell
    }
  })
export default class SellerView extends Vue {
    newSellerModal = false
    showModalSeller = false
    sellerSeleted = {
      id: '',
      name: '',
      identification: '',
      observations: '',
      status: ''
    } as ISeller

    search = ''
    headers = [
      { text: 'Nombre', value: 'name' },
      { text: 'Indentifacion', value: 'identification' },
      { text: 'Observacion', value: 'observations' },
      { text: 'Status', value: 'status' },
      { text: '', value: 'actions', sortable: false, align: 'end' }
    ]

    get items () {
      return this.seller.filter((ISeller: ISeller) => {
        return ((Object.values(ISeller).join(' ').toLowerCase))
      })
    }

    get seller () {
      return useSellerStore().sellers
    }

    set seller (value: ISeller[]) {
      useSellerStore().sellers = value
    }

    async updateData () {
      await Promise.all([
        useSellerStore().fetchAllSeller()
      ])
    }

    async beforeMount () {
      await this.updateData()
    }

    sellerModalMethod (value: boolean) {
      this.newSellerModal = value
    }
}
</script>
  <style>
  .header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  .conteiner-view {
    margin: 16px;
  }

  .create-button {
    background-color: #4DB6AC !important;
    color: white !important;
    font-weight: bold;
    border-radius: 4px;
  }

  .title-table {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .data-table-permissions {
    margin-top: 20px;
  }

  .table-conteiner {
    margin: 32px;
    border-radius: 4px;
    border: #EEE solid 3px !important;
  }
  </style>
