<template>
    <div class="contein-search">
      <v-text-field
        v-model="search"
        label="Buscar imágenes"
        outlined
        dense
        class="search-input"
      ></v-text-field>
      <v-btn  elevation="0" @click="dataFetch" class="primary">Buscar</v-btn>
      <v-row class="image-grid">
        <v-col
          v-for="image in filteredImages"
          :key="image.id"
          cols="12"
          sm="6"
          md="4"
        >
          <v-card class="image-card" @click="toggleLike(image)">
            <v-img
              :src="image.url"
              aspect-ratio="1"
              contain
              class="image"
            ></v-img>
            <v-card-title class="image-title">
              <span>{{ image.title }}</span>
              <v-btn icon @click.stop="toggleLike(image)" class="like-button">
                <v-icon
                  :color="image.liked ? 'red' : 'grey'"
                >
                  {{ image.liked ? 'mdi-heart' : 'mdi-heart-outline' }}
                </v-icon>
              </v-btn>
            </v-card-title>
          </v-card>
        </v-col>
      </v-row>
      <template>
        <div>
          <h1>Carrera de Vendedores</h1>
          <div class="race-track">
            <div class="runner" :style="{ left: runner1Position + '%' }">Vendedor 1</div>
            <div class="runner" :style="{ left: runner2Position + '%' }">Vendedor 2</div>
          </div>
          <button @click="startRace">Comenzar Carrera</button>
        </div>
</template>
    </div>
  </template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      search: '',
      images: [
        {
          id: 1,
          title: 'Imagen 1',
          url: 'https://via.placeholder.com/150',
          liked: false
        },
        {
          id: 2,
          title: 'Imagen 2',
          url: 'https://via.placeholder.com/150',
          liked: false
        },
        {
          id: 2,
          title: 'Imagen 2',
          url: 'https://via.placeholder.com/150',
          liked: false
        },
        {
          id: 2,
          title: 'Imagen 2',
          url: 'https://via.placeholder.com/150',
          liked: false
        }
      ]
    }
  },
  computed: {
    filteredImages () {
      return this.images.filter((image) =>
        image.title.toLowerCase().includes(this.search.toLowerCase())
      )
    }
  },
  methods: {
    toggleLike (image) {
      image.liked = !image.liked
    },

    async dataFetch () {
      const response = await axios.get(`https://www.googleapis.com/customsearch/v1?q=${this.search}&key=AIzaSyBnmEqRoPNf7vdqZ2h78mHe8VmBsRIuI04&cx=64a0ff48aa60342cb`)
      const xdata = await response.data.items.map(item => item.pagemap.cse_image[0].src)
      //  let {data} = await response;
      //  console.log(data);
    }
  }
}
</script>

  <style>
  .search-input {
    padding: 32px;
    margin: 32px;
    width: 500px;
  }

  .image-grid {
    display: flex;
    flex-wrap: wrap;
  }

  .image-card {
    cursor: pointer;
    transition: transform 0.2s ease-in-out;
    margin: 10px;
  }

  .image-card:hover {
    transform: scale(1.05);
  }

  .image {
    border-radius: 8px;
  }

  .image-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .like-button {
    padding: 0;
  }

  .contein-search{
    margin: 32px !important;
  }

  .race-track {
  width: 100%;
  height: 50px;
  position: relative;
  background: #f0f0f0;
}

.runner {
  width: 10px;
  height: 30px;
  background: #0077FF;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  border-radius: 5px;
}

button {
  margin-top: 20px;
}
  </style>
